import { io } from 'socket.io-client';

class SocketioService {
  socket;
  chatRoom ='';
  constructor() {}

  setupSocketConnection() {
    this.socket = io(process.env.VUE_APP_SOCKET_ENDPOINT, {
      // auth: {
      //   token,
      // },
      // preRoom: (localStorage.getItem("chatRoom"))?localStorage.getItem("chatRoom"):""
    });
    console.log(`Connecting socket...`);

    this.socket.on('my broadcast', (data) => {
      console.log(data);
    });
  }

  // subscribeToMessages(cb) {
  //   if (!this.socket) return(true);
  //   this.socket.on('message', msg => {
  //     console.log('Room event received!');
  //     return cb(null, msg);
  //   });
  // }
  receiveMessageStranger(cb){
    if(!this.socket) return true;
    this.socket.on("receiveMessageStranger", data => {
      return cb(null, {
        ...data,
        name: "Người lạ",
      });
    })
  }

  statusRoomStranger(cb){
    if(!this.socket) return true;
    this.socket.on("statusRoomStranger", message => {
      return cb(null, {...message, name: "Bot"});
    })
  }

  nextRoomStranger(){
    //console.log("nextRoom",localStorage.getItem("chatRoom"))
    // if(localStorage.getItem("chatRoom")){
      this.socket.emit("nextRoomStranger",localStorage.getItem("chatRoomStranger"));
    console.log("id",this.socket.id);

    // }
  }
  
  // sendMessage({message, roomName}, cb) {
  //   if (this.socket) this.socket.emit('message', { message, roomName }, cb);
  // }

  sendMessageStranger(message, cb) {
    if(this.socket) this.socket.emit('sendMessageStranger',message,cb);
  }
  
  disconnect() {
    if(this.socket) {
      this.socket.disconnect();
    }
  }
}

export default new SocketioService();



