<template>
    <div class="overlay"></div>
    <div class="modal_form-sign">
        <div class="modal_form-sign-heading">
            <h1>Đăng Ký</h1>
        </div>
        <div class="icon-close">
            <i class="bx bx-x"></i>
        </div>
        <form action="./index.html" class="form-sign">
            <div class="input__field">
                <input id="numberphone" type="text" placeholder="Số điện thoại" />
            </div>
            <div class="input__field">
                <input id="username" type="text" placeholder="Họ và tên" />
            </div>
            <div class="input__field">
                <input id="password" type="password" placeholder="Mật khẩu" />
            </div>
            <div class="input__field">
                <input id="password" type="password" placeholder="Xác nhận lại mật khẩu" />
            </div>
            <button class="btn btn-signup">Đăng ký</button>
        </form>
    </div>
    <!-- modal form sign in -->
    <div class="modal_form-login">
        <div class="modal_form-sign-heading">
            <h1>Đăng Nhập</h1>
        </div>
        <div class="icon-close">
            <i class="bx bx-x"></i>
        </div>
        <form action="index.html" class="form-sign">
            <div class="input__field">
                <input id="numberphone" type="text" placeholder="Số điện thoại" />
            </div>
            <div class="input__field">
                <input id="password" type="password" placeholder="Mật khẩu" />
            </div>
            <button class="btn btn-signup">Đăng Nhập</button>
        </form>
    </div>
    <div class="sidebar">
        <div class="sidebar_list">
            <div class="sidebar_item-search" v-if="statusSidebar !== 'notSignIn'">
                <input type="text" placeholder="Tìm kiếm" />
                <i class="bx bx-search"></i>
            </div>
            <div class="sidebar_item" v-if="statusSidebar === 'notSignIn' || statusSidebar === 'chat'"
                :class="[(statusContent==='chatWithStranger')?'active':'']"
                @click="chatWithStranger()">
                <img src="../assets/avatar-stranger.png" alt="" />

                <div class="sidebar_content">
                    <h3 class="sidebar_name">Người lạ</h3>
                </div>

            </div>
            <div class="sidebar_item" v-if="statusSidebar === 'chat'">
                <img src="https://source.unsplash.com/random" alt="" />
                <div class="sidebar_content">
                    <h3 class="sidebar_name">Nhóm Lập Trình</h3>
                    <span class="sidebar_message">Hello</span>
                </div>
            </div>
            <div class="sidebar_item" v-show="statusSidebar === 'chat'">
                <img src="https://source.unsplash.com/random" alt="" />

                <i class="fa-solid fa-circle"></i>

                <div class="sidebar_content">
                    <h3 class="sidebar_name">Nguyen Van A</h3>
                    <span class="sidebar_message">Hello</span>
                </div>
            </div>
            <div class="content_body-signup" v-show="statusSidebar === 'notSignIn'">
                <button href="#" class="btn btn-sign">Đăng Ký</button>
                <button href="#" class="btn btn-log">Đăng Nhập</button>
            </div>

            <!-- Contacts -->
            <div  v-if="statusSidebar.includes('ontact')" @click="clickContactsSidebar()">
                <div class="sidebar_item"  :class="[(statusSidebar === 'contacts') ? 'active' : '']">
                    <i class="bx bxs-group"></i>
                    <div class="sidebar_content">
                        <h3 class="sidebar_name">Danh sách bạn bè</h3>
                    </div>
                </div>
            </div>
            <div v-if="statusSidebar.includes('ontact')" @click="clickRequestContactSidebar()">
                <div class="sidebar_item" :class="[(statusSidebar === 'requestContact') ? 'active' : '']">
                    <i class="bx bxs-user-plus"></i>
                    <div class="sidebar_content">
                        <h3 class="sidebar_name">Danh sách kết bạn</h3>
                    </div>
                </div>
            </div>
            <div class="note" v-if="statusSidebar === 'notSignIn'">
                *Bằng cách bấm vào phần "Người lạ", bạn có thể trò chuyện ngay với người lạ mà không cần Đăng nhập.
            </div>
        </div>
    </div>

</template>
  
<script>
// import SocketioService from '../services/socketio.service.js';
import { useStore } from '../stores/Store';
import { storeToRefs } from 'pinia'
export default {
    name: 'Sidebar',
    props: {
        // statusSidebar: { type: String, require: true, default: "notSignIn" },
    },
    components: {
    },
    mounted() {
        this.processFormSignInSignUp()
    },
    data() {
        let store = useStore();
        const { statusSidebar } = storeToRefs(store);
        const { statusContent } = storeToRefs(store);
        return {
            statusSidebar,
            statusContent
        };
    },
    methods: {
        processFormSignInSignUp() {
            const btnSign = document.querySelector(".btn-sign");
            const mdalSign = document.querySelector(".modal_form-sign");
            const btnClose = document.querySelectorAll(".icon-close");
            const btnLog = document.querySelector(".btn-log");
            const mdalLog = document.querySelector(".modal_form-login");
            const overlay = document.querySelector(".overlay");
            // khi click vào button đăng ký nào thì modal nào mở ra và mở luôn lớp phủ mờ
            btnSign.addEventListener("click", function () {
                // click open modal
                mdalSign.classList.add("open");
                overlay.classList.add("open");
            });
            // khi click vào button đăng nhập nào thì modal nào mở ra và mở luôn lớp phủ mờ
            btnLog.addEventListener("click", function () {
                // click close modal
                mdalLog.classList.add("open");
                overlay.classList.add("open");
            });
            // khi click vào icon close thì đóng modal lại và tắt lớp phủ mờ
            btnClose.forEach((btn) => {
                btn.addEventListener("click", function () {
                    // click close modal
                    mdalSign.classList.remove("open");
                    mdalLog.classList.remove("open");
                    overlay.classList.remove("open");
                });
            });
        },
        clickContactsSidebar(){
            useStore().clickContactsSidebar();
        },
        clickRequestContactSidebar(){
            useStore().clickRequestContactSidebar();
        },
        chatWithStranger(){
            useStore().chatWithStranger();
        }
    },
    beforeUnmount() {

    }

}
</script>

<style scoped>
    div.note{
        padding: 10px;
        text-align: justify;
        font-size: 14px;
        font-weight: 300;
    }
</style>
