<template>
    <ul class="menu">
        <li class="menu_item">
            <a class="menu_avatar" href="#">
                <img src="https://source.unsplash.com/random" alt="avatar" v-if="statusMenu !== 'notSignIn'" />
                <img src="../assets/redi.svg" alt="avatar" v-else />
            </a>
        </li>
        <li @click="clickChatMenu()" :class="[(statusMenu === 'chat'||statusMenu==='notSignIn') ? 'active' : '']" class="menu_item">
            <div class="icon-message" href="#"><i class="bx bxs-message-rounded-dots"></i></div>
        </li>
        <li @click="clickContactMenu()" class="menu_item" :class="[(statusMenu === 'contacts') ? 'active' : '']"
            v-if="statusMenu !== 'notSignIn'">
            <a class="icon-message"><i class="bx bxs-contact"></i></a>
        </li>
        <li class="menu_item menu_item-logout" v-if="statusMenu !== 'notSignIn'" @click="signOut()">
            <div class="icon-message"><i class="bx bxs-log-out"></i></div>
        </li>
    </ul>
</template>
  
<script>
// import SocketioService from '../services/socketio.service.js';
import { useStore } from '../stores/Store';
import { storeToRefs } from 'pinia'
export default {
    name: 'Menu',
    props: {
        // statusMenu: {type: String, require: true, default: "notSignIn"},
    },
    components: {
    },
    mounted() {

    },
    data() {
        let store = useStore();
        const { statusMenu } = storeToRefs(store);
        return {
            statusMenu,
        };
    },
    watch: {

    },
    methods: {
        clickContactMenu() {
            useStore().clickContactMenu();
        },
        clickChatMenu() {
            if(this.statusMenu !== 'notSignIn'){
                useStore().clickChatMenu();
            } else {
                alert("Bạn phải đăng nhập để thực hiện chức năng này !");
            }
        },
        signOut(){
            if(confirm("Bạn muốn đăng xuất khỏi tài khoản này ?")){
                useStore().signOut();
            }
        }
    },
    beforeUnmount() {

    }

}
</script>